<template>
  <div id="app">
    <Header :isSmallHeader="isSmallHeader"></Header>

    <router-view @makeSmallHeader="makeSmallHeader"/>

    <Footer></Footer>
  </div>
</template>

<script>
import Header from '@/components/common/header/Header'
import Footer from '@/components/common/footer/Footer'

export default {
  data: () => ({
    isSmallHeader: false,
  }),
  components: {
    Header,
    Footer,
  },
  watch: {
    $route(to, from) {
      document.title = (this.$route.name ? `Третий модуль - ${this.$route.name}` : 'Третий модуль')
      this.isSmallHeader = false
    },
  },
  methods: {
    makeSmallHeader() {
      this.isSmallHeader = true
    },
  },
}
</script>

<style scoped>
  @import url('./assets/css/all.min.css');
  @import url('./assets/css/style.min.css');
</style>
