import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use( VueRouter )

const routes = [
  {
    path: '/',
    name: 'Главная',
    component: () => import( '@/views/Index' )
  },
  {
    path: '/search',
    name: 'Поиск рейсов',
    component: () => import( '@/views/Search' )
  },
  {
    path: '*',
    name: 'Ошибка 404',
    component: () => import( '@/views/404' )
  },
]

let router = new VueRouter( {
  mode: 'history',
  routes,
} )

export default router
